import React, { useEffect, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, ScrollView, FlatList, TouchableOpacity, Image } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { CustomButton } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics, Images, Colors } from '../../../Theme'

import { SectionGrid } from 'react-native-super-grid';

class Pens extends Component {
  constructor(props){
    super(props);

    const {Sections, Mid} = this.setData(props)
    this.state = {
      Mid: Mid,
      Sections: Sections
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;

    var Sections = {}
    if (localDataStore && localDataStore.sections) {
      Sections = sortObject(Props.firebase.localDataStore.sections)
    }

    var Mid = 0;
    if (localDataStore && localDataStore.weighings) {
      for (const property in localDataStore.weighings) {
        if (localDataStore.weighings[property] && Object.keys(localDataStore.weighings[property]).length > 0){
          var weight = localDataStore.weighings[property][Object.keys(localDataStore.weighings[property])[0]].weight
          if (weight){
            if (Mid > 0){
              Mid = ((Mid + weight)/2)
            } else {
              Mid = (Mid + weight)
            }
          }
        }
      }
    }

    return {Sections, Mid}
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    this.props.getWeights(Object.keys(localDataStore.userData.farms)[0])
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null){
      NavigationService.navigate("Auth")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidMount() {
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if (this.props.firebase.localDataStore.sections && JSON.stringify(this.state.Sections)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.sections))) {
          const {Sections, Mid} = this.setData(this.props)
          this.setState({
            Mid: Mid,
            Sections: Sections
          })
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    return(
      <View
      style={[
        Helpers.mainContainer,
      ]}
      >
        <View style={[Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {firebaseUser && this.state.Sections? (
            <ScrollView style={[Metrics.verticalMargin, {paddingRight: 5}]}>
              <SectionGrid
                itemDimension={70}
                spacing={10}
                sections={
                  Object.keys(this.state.Sections).map((Section) => {
                    var Pens = sortObject(this.state.Sections[Section].pens)
                    return(
                      {
                        title: Section,
                        data: Object.keys(Pens)
                      }
                    )
                  })
                }

                renderSectionHeader={({ section }) => (
                  <View style={[Helpers.buttonContainer, Helpers.midWidth, {marginBottom:0}]}>
                    <CustomButton Text={section.title} buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
                      onPress={() => {} }/>
                  </View>
                )}
                renderItem={({ item }) => {
                  var color = "white"
                  if (localDataStore.weighings && localDataStore.weighings[item]){
                    var Weighing = localDataStore.weighings[item][Object.keys(localDataStore.weighings[item])[0]];
                      if (Weighing){
                      var weight = localDataStore.weighings[item][Object.keys(localDataStore.weighings[item])[0]].weight
                      if (weight && !isNaN(weight)){
                        color = Colors.success
                
                        var _Mid = this.state.Mid
                        if (weight > _Mid+10 || weight < _Mid-10){
                          color = Colors.error
                        }
                      }
                    }
                  }
                  return(
                  <TouchableOpacity style={[Helpers.borderContainer, {width:70, height:70, alignItems:"center", justifyContent:"center", backgroundColor:color}]}
                    onPress={() => {
                      this.props.setCurrentData(item)
                      NavigationService.navigate("DataDisplay")
                    }}>
                    <Text>{item}</Text>
                  </TouchableOpacity>
                  )
                }}
              />
            </ScrollView>
          ):(
            <View style={[Helpers.fillRowCenter, Helpers.center, Helpers.logoContainer]}>
              <Image style={Helpers.fullSize} source={Images.Hexa_icon} resizeMode={'contain'} />
            </View>
          )}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    getWeights: farmid => dispatch(FirebaseActions.fetchStarting(Actions.FETCH, (DataRefs.SECTIONS + DataRefs.WEIGHING), [farmid])),
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),

    setCurrentData: dataId => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, dataId: dataId})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Pens);
//export default Pens;
