class DataDisplay extends Component {
  constructor(props){
    super(props);

    this.state = {
    }
  }

  getData() {
    this.props.setFirebaseError(null)

    if (this.props.firebase && this.props.firebase.localDataStore){
      const localDataStore = this.props.firebase.localDataStore
      if (this.props.firebase.currentData && this.props.firebase.currentData.type){
        const CurrentData = this.props.firebase.currentData;
        switch(CurrentData.type){
          case DataRefs.PENS:
            this.props.getWeights(Object.keys(localDataStore.userData.farms)[0], CurrentData.dataId)
            break;
          case DataRefs.PIGS:
            //DataView = PigData_View(this, CurrentData, localDataStore)
            break;
        }
      }
    }
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null){
      NavigationService.navigate("Auth")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidMount() {
    this.checkData(this.props);

    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid
    const CurrentData = this.props.firebase.currentData

    var DataView = (<View></View>);

    switch(CurrentData.type){
      case DataRefs.PENS:
        DataView = PenData_View(this, CurrentData, localDataStore)
        break;
      case DataRefs.PIGS:
        DataView = PigData_View(this, CurrentData, localDataStore)
        break;
    }

    return(
      <View
      style={[
        Helpers.mainContainer,
      ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {DataView}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

function onPageSwipe(Control, localDataStore, CurrentData, dir) {
  //const pen = localDataStore.pens[CurrentData.dataId]
  //console.log(CurrentData.data.datas)
  //const section = localDataStore.sections[CurrentData.data.datas.section].pens
  const section = localDataStore.pens[CurrentData.dataId].section
  const Pens = sortObject(localDataStore.sections[section].pens)

  if (CurrentData.type === DataRefs.PENS && CurrentData.data && Pens[CurrentData.dataId]) {
    var index = Object.keys(Pens).indexOf(CurrentData.dataId)

    switch (dir) {
      case true: //forward
        index = index +1
        break;
      case false: //backward
        index = index -1
        break;
    }

    if (index < 0) index = Object.keys(Pens).length - 1
    else if (index >= Object.keys(Pens).length) index = 0

    Control.props.setCurrentData(Object.keys(Pens)[index])
    NavigationService.replace("DataDisplay")
  }
}

