function getRandomNumber(low, high) {
  var r = Math.floor(Math.random() * (high - low + 1)) + low;
  return r;
}

function getRandomColor() {
  var red_range = [10, 250];
  var green_range = [10, 250];
  var blue_range = [10, 250];
  var a_range = [0.2, 1];

  var red = getRandomNumber(red_range[0], red_range[1]);
  var green = getRandomNumber(green_range[0], green_range[1]);
  var blue = getRandomNumber(blue_range[0], blue_range[1]);

  var alpha = getRandomNumber(a_range[0] * 100, a_range[1] * 100) / 100;

  return {
    r: red,
    g: green,
    b: blue,
    a: alpha,
    rgbaValue: 'rgba(' + red + ',' + green + ',' + blue + ',' + alpha + ')'
  }
}

const window = Dimensions.get("window");
const screen = Dimensions.get("screen");

function WeightsToGraphData(Legends, Labels, PenWeighings) {
  var DataSets = []

  Legends.forEach(Pen => {
    if (PenWeighings[Pen]) {
      var Weighings = PenWeighings[Pen];
      if (Object.keys(Weighings).length >= 1) {
        var DataSet = []
        var DataSetX = []
        Object.keys(Weighings).forEach(Weighing => {
          var data = Weighings[Weighing]
          if (data.weight){
            DataSet.push(data.weight)
            DataSetX.push(data.time)
          }
        })

        while(DataSet.length < Labels.length) {
          DataSet.push(null)
          DataSetX.push(null)
        }

        var color = getRandomColor();
        DataSets.push({data: DataSet, dataX: DataSetX, color:(opacity = 1) => color.rgbaValue})
      }
    }
  });
  return DataSets;
}

class DataDisplay extends Component {
  constructor(props){
    super(props);

    const {Labels, xMinMax, Legends, DataSets, TempGraph, Graphs} = this.setData(props)
    this.state = {
      dimensions: {window, screen},

      Labels: Labels,
      xMinMax: xMinMax,
      Legends: Legends,
      DataSets: DataSets,
      TempGraph: TempGraph,
      Graphs: Graphs,

      ActiveCurve: null,
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;

    var Labels = []
    var xMinMax = [1577833200000, 1609369200000]
    var Legends = []
    var DataSets = []

    var TempGraph = null
    var Graphs = null

    if (localDataStore && localDataStore.graphs) {
      Graphs = localDataStore.graphs;
  
      if (Graphs.gcurve){
        TempGraph = Graphs.gcurve
        Legends.push("Batch template")
        DataSets.push({
          data: TempGraph.data,
          color: (opacity = 0.1) => `rgba(0, 0, 0, ${opacity})`, // optional
        })
        Labels = TempGraph.labels // Agressive overtake...
      }
    }

    if (localDataStore && localDataStore.weighings) {
      Legends = Legends.concat(Object.keys(localDataStore.weighings))
      Legends = Legends.filter(e => e !== '__jsogObjectId');
      DataSets = DataSets.concat(WeightsToGraphData(Legends, Labels, localDataStore.weighings))
    }

    return {Labels, xMinMax, Legends, DataSets, TempGraph, Graphs}
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    this.props.getCurvesWeights(Object.keys(localDataStore.userData.farms)[0], "default")
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null) {
      NavigationService.navigate("Auth")
    }
    //Mabye call check for localdataStore get farms..
  }

  onChange = ({ window, screen }) => {
    this.setState({
      dimensions: {window, screen},
    })
  };

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if (this.props.firebase.localDataStore.graphs && this.props.firebase.localDataStore.weighings) {
          const {Labels, xMinMax, Legends, DataSets, TempGraph, Graphs} = this.setData(this.props)
          this.setState({
            Labels: Labels,
            xMinMax: xMinMax,
            Legends: Legends,
            DataSets: DataSets,
            TempGraph: TempGraph,
            Graphs: Graphs,
          })
        }
      }
    }
  }

  componentDidMount() {
    this.checkData(this.props);
    this.getData();
    Dimensions.addEventListener("change", this.onChange);
  }

  componentWillUnmount(){
    Dimensions.removeEventListener("change", this.onChange);
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    var wide = this.state.dimensions.window.width>1000?true:false
    var height = ((this.state.dimensions.window.height - (70*2)) - (Metrics.medium * 3)) - (80)
    var width = ((this.state.dimensions.window.width - (Metrics.normal * 2))*2) / (wide?2.05:1)

    console.log(height)

    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin,] }>
            <CustomButton Text="Batch" buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
              onPress={() => {} }/>
          </View>
          <View style={[Helpers.contentContainer, Helpers.fullSize, {marginHorizontal:0, paddingHorizontal:-20}]}>
            <ScrollView horizontal={true} showsHorizontalScrollIndicator={wide?false:true} style={[Helpers.fullSize]}>
              {this.state.Graphs ?(
              <LineChart
                //bezier
                style={[{marginRight:Metrics.normal}]}
                width={width}
                height={height}

                data={{
                  labels: this.state.Labels,
                  xMinMax: this.state.xMinMax,
                  legend: this.state.Legends.slice(0, this.state.DataSets.length),
                  datasets: this.state.DataSets,
                  height: height
                }}

                onDataPointClick={(data) => {
                  console.log("here")
                  console.log(data) 
                }}

                withShadow={false}
                withVerticalLines={true}
                fromZero={false}
                segments={10}
                
                verticalLabelRotation={0} //Not Working in browser
                xLabelsOffset={0}
                height={height}

                chartConfig={{
                  backgroundColor: Colors.white,
                  backgroundGradientFrom: Colors.white,  //#FFFFFF
                  backgroundGradientTo: Colors.white,     
                  decimalPlaces: 0, // optional, defaults to 2dp
                  color: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`,  //Lines / Grid
                  labelColor: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`, //Lables
                  style: {
                    borderRadius: 16,
                    height: height,
                  },
                  propsForDots: {
                    r: "5",
                    strokeWidth: "0.5",
                    stroke: Colors.white
                  }
                }}
              />):(<></>)}
            </ScrollView>
          </View>
          <View style={[Helpers.buttonContainer, Helpers.fullWidth, Helpers.rowCenter, {maxHeight:100}] }>
            <View style={[Helpers.column, Helpers.borderContainer, {borderRadius:0}]}>
              <Text>Text 1</Text>
            </View>
            <View style={[Helpers.colCenter, Helpers.borderContainer, {borderRadius:0}]}>
              <Text>Text 2</Text>
            </View>
            <View style={[Helpers.colCenter, Helpers.borderContainer, {borderRadius:0}]}>
              <Text>Text 3</Text>
            </View>
          </View>
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),

    getCurvesWeights: (farmid, curve) => dispatch(FirebaseActions.fetchStarting(Actions.FETCH, (DataRefs.GRAPH + DataRefs.WEIGHING), [farmid, curve, 99]))
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(DataDisplay);
